body {
    margin: 0;
    padding: 0;
}
.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
.ImgList {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.ImgList__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.ImgCard {
    margin: 20px;
    box-sizing: border-box;
    padding: 10px;
    border: 1px solid;
    border-radius: 5px;
}

.ImgCard__img {
    height: 300px;
}

